import axios from "./api/axios";

const RegionsService = {
  async fetchRegions() {
    try {
      return await axios.get("api/info/regions/");
    } catch (e) {
      console.log(e);
    }
  },
  async fetchDistrictsByRegionId(val) {
    try {
      return await axios.get(`/api/info/districts/ByRegionId/`, {
        params: {
          region: val.target.value,
        },
      });
    } catch (e) {
      console.log(e);
    }
  },
  async fetchMahallaByDistrictId(val) {
    try {
      return await axios.get("api/info/mahalla/ByDistrictId/", {
        params: {
          district: val.target.value,
        },
      });
    } catch (e) {
      console.log(e);
    }
  },
};
export default RegionsService;
