import { onMounted, ref } from "vue";
import RegionsService from "../../services/regions-districts.service";

export default function useRegion(regionModel) {
  const regionsList = ref([]);
  const fetchRegions = async () => {
    const { data } = await RegionsService.fetchRegions();
    regionsList.value = data;
  };
  onMounted(async () => {
    await fetchRegions();
  });

  return {
    regionsList,
  };
}
