<template>
  <div class="w-full rounded-md bg-white px-[20px] py-[16px]">
    <PageTitle>
      Tizimga markaz qo'shish
    </PageTitle>
    <AddCenterForm :clear-form-mode="clearForm" @submit="handleSubmit" />
  </div>
</template>

<script setup>
import axios from "../services/api/axios";
import useAlert from "../composables/useAlert";
import AddCenterForm from "../components/AddCenterForm";
import PageTitle from "../components/LayoutItems/PageTitle";
import useErrorAlert from "../composables/useErrorAlert";
import useClearForm from "../composables/useClearForm";

const {alertIt} = useAlert()
const {alertThisError} = useErrorAlert()
const {clearForm,setClearForm} = useClearForm()

const handleSubmit = async (formData) => {
  try {
    const {data:{message}} = await axios.post('/api/info/markazs/', formData)
    if(message){
      alertIt(message)
    }
    setClearForm(true)
  } catch (e) {
    alertThisError(e)
  }
}



</script>

<style scoped>
span {
  @apply text-red-400 h-5
}
</style>
