<template>
  <form @submit="onSubmit">
    <div class="grid grid-cols-2 gap-3 w-full lg:w-[85%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="name"> Markaz nomi </label>
        <input v-validate-cyrillic id="name" v-model="name" name="name" class="base_input" placeholder="Markaz nomini kiriting" />
        <span>{{ nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="region"> Markaz joylashgan viloyat / shahar </label>
        <SelectBox v-model="region" :options="regionsList" track-by="id" show-by="name" placeholder="Viloyat / Shaharni tanlang" />
        <span>{{ regionError }}</span>
      </div>
    </div>
    <button type="submit" class="base_input bg-primary text-white border-primary">Saqlash</button>
  </form>
</template>

<script setup>
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import useRegion from '../composables/selectbox-list/useRegion';
import {requiredValidatorMessage} from '../utils/validatorMessages';
import {defineEmits, defineProps, toRefs, watch} from 'vue';
import SelectBox from './Common/SelectBox';

const emit = defineEmits(['submit']);
const props = defineProps({
  clearFormMode: {
    type: Boolean,
    default: false,
  },
  initialFields: {
    type: Object,
    default: function () {
      return {};
    },
  },
});

const {clearFormMode} = toRefs(props);
watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);
const {regionsList} = useRegion();

const schema = yup.object({
  name: yup.string().required(requiredValidatorMessage),
  region: yup.string().required(requiredValidatorMessage),
});

const {handleSubmit, resetForm} = useForm({
  validationSchema: schema,
});

const onSubmit = handleSubmit(async ({name, region}) => {
  const formData = new FormData();
  formData.append('name', name);
  formData.append('region', region);
  emit('submit', formData);
});

const {value: name, errorMessage: nameError} = useField(
  'name',
  {},
  {
    initialValue: '',
  }
);

const {value: region, errorMessage: regionError} = useField(
  'region',
  {},
  {
    initialValue: '',
  }
);
</script>

<style scoped></style>
