export const isLatinLetters = (args) => [
  'is-latin',
  'Iltimos faqat lotin harflaridan foydalaning',
  (value) => {
    const regex = /[^\P{L}a-z][^a-z]*/giu;
    return !regex.test(value);
  },
];
