import useAlert from './useAlert';

export default function useErrorAlert() {
  const {alertIt} = useAlert();
  const alertThisError = (error) => {
    console.log({error}, 'UseErrorAlert');
    if (typeof error?.response?.data?.message === 'string') {
      alertIt(error?.response?.data?.message, 'error');
    } else if (error?.response?.data && typeof error?.response?.data !== 'string' && error?.response?.data?.messages) {
      Object.values(error.response.data.messages).map((errItem) => {
        errItem?.message ? alertIt(errItem.message, 'error') : null;
      });
    } else if (Array.isArray(error?.response?.data)) {
      error?.response?.data.map((errItem) => {
        alertIt(errItem, 'error');
      });
    } else if (error?.response?.data && error?.response?.status === 400) {
      Object.values(error.response.data).map((errItem) => {
        typeof errItem == 'string' ? alertIt(errItem, 'error') : errItem.map((n) => alertIt(n, 'error'));
      });
    } else {
      alertIt("Server bilan bog'liq xatolik yuz berdi.", 'error');
    }
  };
  return {
    alertThisError,
  };
}
